<template>
  <div
    id="app"
    tabIndex="0"
    @keydown="keydownEvent($event.code)"
    @keyup="keyupEvent($event.code)"
  >
    <div class="loading" v-if="!loadingDone">
      loading resources... {{ loading }}%
    </div>
    <div class="progress">
      <div class="bar" :style="{ width: progress }"></div>
    </div>
    <!-- <button @click="play" class="btn">播放(play)</button>
    <button @click="stop" class="btn">停止(stop)</button> -->

    <!-- <select name="" id="" v-model="selectedSong">
      <option v-for="song in songs" :value="song" :key="song">
        {{ song }}
      </option>
    </select> -->

    <!-- 这里的按键只对鼠标作用 -->
    <div
      class="piano"
      @mousedown="mousedownEvent($event.target.attributes.note)"
      @mouseup="mouseupEvent($event.target.attributes.note)"
      @mousemove="mousemoveEvent($event.target.attributes.note)"
    >
      <div class="octave first">
        <div class="white" :class="{ active: activeKeys[1] }" :note="1"></div>
        <span class="black" :class="{ active: activeKeys[2] }" :note="2"></span>
        <div class="white" :class="{ active: activeKeys[3] }" :note="3"></div>
      </div>
      <div class="octave" v-for="n in 7" :key="n">
        <div
          class="white"
          :class="{ active: activeKeys[(n - 1) * 12 + 4] }"
          :note="(n - 1) * 12 + 4"
        ></div>
        <span
          class="black"
          :class="{ active: activeKeys[(n - 1) * 12 + 5] }"
          :note="(n - 1) * 12 + 5"
        ></span>
        <div
          class="white"
          :class="{ active: activeKeys[(n - 1) * 12 + 6] }"
          :note="(n - 1) * 12 + 6"
        ></div>
        <span
          class="black"
          :class="{ active: activeKeys[(n - 1) * 12 + 7] }"
          :note="(n - 1) * 12 + 7"
        ></span>
        <div
          class="white"
          :class="{ active: activeKeys[(n - 1) * 12 + 8] }"
          :note="(n - 1) * 12 + 8"
        ></div>
        <div
          class="white"
          :class="{ active: activeKeys[(n - 1) * 12 + 9] }"
          :note="(n - 1) * 12 + 9"
        ></div>
        <span
          class="black"
          :class="{ active: activeKeys[(n - 1) * 12 + 10] }"
          :note="(n - 1) * 12 + 10"
        ></span>
        <div
          class="white"
          :class="{ active: activeKeys[(n - 1) * 12 + 11] }"
          :note="(n - 1) * 12 + 11"
        ></div>
        <span
          class="black"
          :class="{ active: activeKeys[(n - 1) * 12 + 12] }"
          :note="(n - 1) * 12 + 12"
        ></span>
        <div
          class="white"
          :class="{ active: activeKeys[(n - 1) * 12 + 13] }"
          :note="(n - 1) * 12 + 13"
        ></div>
        <span
          class="black"
          :class="{ active: activeKeys[(n - 1) * 12 + 14] }"
          :note="(n - 1) * 12 + 14"
        ></span>
        <div
          class="white"
          :class="{ active: activeKeys[(n - 1) * 12 + 15] }"
          :note="(n - 1) * 12 + 15"
        ></div>
      </div>
      <div class="octave last">
        <div class="white" :class="{ active: activeKeys[88] }" :note="88"></div>
      </div>
    </div>
    <div class="version">piano for wps</div>
  </div>
</template>

<script>
import { getPianoKeyCode } from "../public/js/libs/keyMap.js";
import { selectCellByAddr, setColorByAddr } from "../public/js/libs/4wps.js";
export default {
  data() {
    return {
      loading: 0,
      loadingDone: false,
      progress: 0,
      timer: null,
      totalTime: 0,
      startTime: 0,
      activeKeys: {},
      // selectedSong: "Butterfly.mid",
      // songs: ["Butterfly.mid"],
      noteBefore: -1, //鼠标原先note
      pullDown: false, //是否已经按下(鼠标)
      keyDown: [], //已经按下的按键(键盘)
      PlayStatus: 0, //播放状态 0停止 1播放 2暂停
      pauseLong: 0,
      pauseTime: 0,
      MuscrTimer: null,
    };
  },
  watch: {
    selectedSong() {},
  },
  methods: {
    initTimer() {
      this.startTime = +new Date();
      this.totalTime = MIDI.Player.endTime;
      this.timer = setInterval(() => {
        //暂停进度条
        if (this.PlayStatus == 2) {
          return;
        }
        // let timeElapsed = new Date() - this.startTime;
        let timeElapsed = new Date() - this.startTime - this.pauseLong;
        if (timeElapsed / this.totalTime >= 1) {
          //这里结束 刷新按钮状态
          this.stopTimer();
          this.PlayStatus = 0;
          wps.PluginStorage.setItem("MSEcommand", "pianoFinish");
          return;
        }
        this.progress = (100 * timeElapsed) / this.totalTime + "%";
      }, 20);
    },
    stopTimer() {
      let timer = this.timer;
      clearInterval(timer);
      this.MuscrTimer = null;
      this.progress = 0;
      //this.timer = null
    },
    activate(note) {
      note = Number(note);
      if (this.activeKeys[note]) {
        this.deactivate(note);
        this.$nextTick(() => {
          setTimeout(() => {
            this.$set(this.activeKeys, note, true);
          }, 20);
        });
      } else {
        this.$set(this.activeKeys, note, true);
      }
    },
    deactivate(note) {
      note = Number(note);
      this.$delete(this.activeKeys, note);
    },
    //============ dev for wps ===========
    //鼠标事件-按下
    mousedownEvent(note) {
      if (!note) return;
      note = typeof note == "number" ? note : note.value;
      this.activate(note);
      this.pullDown = true;
      this.noteBefore = Number(note);
      //抬起的时候 播放鼠标的这个音
      //通道(0钢琴)/按键(音)/声音力度/延迟
      MIDI.noteOn(0, Number(note) + 20, 200, 0.005);
      //console.log(Number(note) + 20);
    },
    //鼠标事件-抬起
    mouseupEvent(note) {
      if (!note) note = this.noteBefore;
      note = typeof note == "number" ? note : note.value;
      this.deactivate(note);
      this.pullDown = false;
    },
    //鼠标事件-滑动
    mousemoveEvent(note) {
      if (note && this.pullDown) {
        note = Number(note.value);
        if (this.noteBefore != note) {
          this.mouseupEvent(this.noteBefore);
          this.mousedownEvent(note);
        }
      }
    },
    //键盘事件-按下
    keydownEvent(code) {
      if (this.keyDown.includes(code)) return; //不能同时按
      this.keyDown.push(code);
      var pianoKey = getPianoKeyCode(code);
      MIDI.noteOn(0, pianoKey, 200, 0.005);
      this.activate(pianoKey - 20);
    },
    //键盘事件-抬起
    keyupEvent(code) {
      if (this.keyDown.includes(code)) {
        this.keyDown.remove(this.keyDown.indexOf(code));
        this.deactivate(getPianoKeyCode(code) - 20);
      }
    },
    play() {
      // 这里改成直接传midi文件
      let midiUrl = wps.PluginStorage.getItem("midiUrl");
      let player = MIDI.Player;
      player.timeWarp = 1;
      if (this.PlayStatus == 2) {
        player.start();
        this.pauseLong = this.pauseLong + (new Date() - this.pauseTime);
      } else if (this.PlayStatus == 0) {
        this.startTime = 0;
        this.pauseTime = 0;
        this.pauseLong = 0;
        this.stopTimer();
        player.stop();
        player.wpsLoadFile(midiUrl, () => {
          player.start();
          this.initTimer();
        });
      }
      this.PlayStatus = 1;
    },
    //============ dev for wps Muscr ===========
    pause() {
      let player = MIDI.Player;
      player.pause();
      this.PlayStatus = 2;
      this.pauseTime = new Date();
    },
    stop() {
      this.stopTimer();
      let player = MIDI.Player;
      player.stop();
      this.PlayStatus = 0;
      setTimeout(() => {
        this.stopTimer();
      }, 100);
    },
    playMuscr() {
      // // 原先方法舍弃一下 换成递归调用 -> initMuscrTimer() ----2021-9-8
      // let pianoObj = wps.PluginStorage.getItem("pianoObj");
      // pianoObj = eval("(" + pianoObj + ")");
      // let TrackObj = pianoObj.TrackObj;
      // TrackObj.forEach((obj) => {
      //   let pianoKey = obj.code;
      //   let code = obj.code;
      //   if (pianoKey == -1) return;

      //   //模拟按下
      //   MIDI.noteOn(0, pianoKey, 200, obj.t / 1000);
      //   setTimeout(() => {
      //     this.keyDown.push(code);
      //     this.activate(pianoKey - 20);
      //   }, obj.t);

      //   //模拟抬起
      //   setTimeout(() => {
      //     if (this.keyDown.includes(code)) {
      //       this.keyDown.remove(this.keyDown.indexOf(code));
      //       this.deactivate(pianoKey - 20);
      //     }
      //   }, obj.t + obj.long / 1.1);
      // });

      if (this.PlayStatus == 2) {
        //暂停
        this.pauseLong = this.pauseLong + (new Date() - this.pauseTime);
      } else if (this.PlayStatus == 0) {
        //停止
        this.startTime = 0;
        this.pauseTime = 0;
        this.pauseLong = 0;
        this.stopTimer();
        this.initMuscrTimer();
      }
      this.PlayStatus = 1; //播放
    },
    initMuscrTimer() {
      var app = wps.WpsApplication();
      // app.ActiveDocument.Unprotect("passW0rd"); //先解锁然后操作
      app.ScreenUpdating = true;

      let pianoObj = wps.PluginStorage.getItem("pianoObj");
      pianoObj = eval("(" + pianoObj + ")");
      let TrackObj = pianoObj.TrackObj;

      this.startTime = +new Date();
      this.totalTime = pianoObj.total;
      let haspassedTime = 0; //代表歌曲已经播放多久了
      let p = 0; //指针 播放到第几个noteObj了
      this.MuscrTimer = function () {
        setTimeout(() => {
          if (this.MuscrTimer) this.MuscrTimer();
          //暂停进度条
          if (this.PlayStatus == 2) {
            return;
          }
          haspassedTime += 20;

          if (p < TrackObj.length && haspassedTime >= TrackObj[p].t) {
            let pianoKey = TrackObj[p].code;
            if (pianoKey != -1) {
              //模拟按下
              MIDI.noteOn(0, pianoKey, 200, 0.005);
              this.keyDown.push(pianoKey);
              selectCellByAddr(TrackObj[p].addr);
              this.activate(pianoKey - 20);
              // setColorByAddr(TrackObj[p].addr, 6619135);

              //模拟抬起
              setTimeout(() => {
                if (this.keyDown.includes(pianoKey)) {
                  this.keyDown.remove(this.keyDown.indexOf(pianoKey));
                  this.deactivate(pianoKey - 20);
                  // setColorByAddr(TrackObj[p].addr, -16777216);
                }
              }, TrackObj[p].long / 1.1);
            }
            p += 1;
          }

          if (haspassedTime / this.totalTime >= 1) {
            //这里结束 刷新按钮状态
            this.stopTimer();
            this.PlayStatus = 0;
            // app.ActiveDocument.Protect(3, false, "passW0rd"); //播放完毕后保护该文档
            app.ActiveDocument.Select();
            app.ActiveWindow.Selection.EndKey();
            wps.PluginStorage.setItem("MSEcommand", "pianoFinish");
            return;
          }
          this.progress = (100 * haspassedTime) / this.totalTime + "%";
        }, 20);
      };
      this.MuscrTimer();
    },
  },
  created() {},
  mounted() {
    let i = 0;
    let vm = this;
    MIDI.loadPlugin({
      soundfontUrl: "./soundfont/",
      instrument: "acoustic_grand_piano",
      onprogress: function (state, progress) {
        vm.loading = parseInt(progress * 100);
        if (progress == 1 && state == "load") {
          vm.loadingDone = true;
        }
      },
      onsuccess: function () {
        let player = MIDI.Player;
        player.addListener(function (data) {
          if (data.message === 144) {
            // console.log(data);
            vm.activate(data.note - 20);
          }
          if (data.message === 128) {
            vm.deactivate(data.note - 20);
          }
        });
      },
    });

    var commandInterval = () => {
      setTimeout(() => {
        commandInterval();
        var command = wps.PluginStorage.getItem("pianoCommand");
        // console.log("command =", command);
        if (!command) return;
        clearPianoCommand();
        console.log("command =", command);
        switch (command) {
          case "play":
            this.play();
            break;
          case "pause":
            this.pause();
            break;
          case "stop":
            this.stop();
            break;
          case "playMuscr":
            this.playMuscr();
            break;
        }
      }, 369);
    };
    function clearPianoCommand() {
      wps.PluginStorage.setItem("pianoCommand", null);
    }
    commandInterval();

    //给数组添加个remove方法
    Array.prototype.remove = function (from, to) {
      var rest = this.slice((to || from) + 1 || this.length);
      this.length = from < 0 ? this.length + from : from;
      return this.push.apply(this, rest);
    };
  },
};
</script>
<style lang="less" scoped>
@whiteWidth: (100/52);
@whiteHeight: (25/4.16667) * @whiteWidth;
@blackWidth: (2.77778/4.16667) * @whiteWidth;
@blackHeight: (16.6667/2.77778) * @blackWidth;
#app {
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgb(48, 48, 48);
  .loading {
    position: fixed;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 25px;
    background-color: rgba(255, 255, 255, 0.8);
    z-index: 10000;
  }
  .btn {
    // margin-right: 20px;
    /*display: inline-block;
      width: 70px;
      height: 36px;
      background-color: gray;*/
  }
  .progress {
    z-index: 50;
    height: 5px;
    box-sizing: border-box;
    background-color: rgb(48, 48, 48);
    width: 100%;
    box-shadow: rgba(255, 255, 255, 0.4) 0px 1px inset;
    background-image: linear-gradient(
      rgba(255, 255, 255, 0.3),
      rgba(255, 255, 255, 0) 50%,
      rgba(0, 0, 0, 0.12) 51%,
      rgba(0, 0, 0, 0.04)
    );
    border-width: 1px;
    border-style: solid;
    border-color: initial;
    border-image: initial;
    border-radius: 1px;
    .bar {
      // display: inline-block;
      height: 100%;
      background-image: linear-gradient(
        rgba(118, 255, 53, 0.3),
        rgb(38, 255, 83)
      );
    }
  }
}
.piano {
  // position: absolute;   //for wps
  // bottom: 0;
  left: 0;
  width: 100%;

  display: flex;
  .octave {
    position: relative;
    display: flex;
    flex-shrink: 7;
    flex-grow: 7;
    &.first {
      flex-shrink: 2;
      flex-grow: 2;
      .white {
        padding-top: @whiteHeight*7*7 / 2%;
      }
      .black {
        width: @blackWidth * 7 * 7/2%;
        padding-top: @blackHeight * 7 * 7/2%;
        &:first-of-type {
          left: 41.5%;
        }
      }
    }
    &.last {
      flex-shrink: 1;
      flex-grow: 1;
      .white {
        padding-top: @whiteHeight*7*7%;
      }
    }
  }
  .black {
    cursor: pointer;
    position: absolute;
    box-sizing: border-box;
    box-shadow: rgba(0, 0, 0, 0.3) 2px 2px 2px;
    z-index: 100;
    width: @blackWidth * 7%;
    //height: @blackHeight * 1vw;
    padding-top: @blackHeight * 7%;
    transform-origin: center top 0px;
    transform: translate(0px, 0px) rotateX(0deg);
    border-width: 1px;
    border-style: solid;
    border-color: grey;
    border-image: initial;
    border-radius: 0px 0px 5px 5px;
    background: black;
    transition: all 0.003s ease 0s;
    &.active {
      transform: rotateX(-1deg) scale(0.95);
      background-color: rgb(50, 125, 255);
    }
    &:first-of-type {
      left: 8.5%;
    }
    &:nth-of-type(2) {
      left: 25.5%;
    }
    &:nth-of-type(3) {
      left: 50.6%;
    }
    &:nth-of-type(4) {
      left: 67%;
    }
    &:nth-of-type(5) {
      left: 84%;
    }
  }

  .white {
    flex: 1;
    box-sizing: border-box;
    width: @whiteWidth*7%;
    cursor: pointer;
    box-sizing: border-box;
    box-shadow: rgba(0, 0, 0, 0.3) 2px 2px 2px;
    z-index: 90;
    //height: @whiteHeight*1vw;
    height: 0;
    padding-top: @whiteHeight*7%;
    transform-origin: center top 0px;
    transform: translate(0px, 0px) rotateX(0deg);
    border-width: 1px;
    border-style: solid;
    border-color: grey;
    border-image: initial;
    border-radius: 0px 0px 5px 5px;
    background: white;
    transition: all 0.003s ease 0s;
    &.active {
      transform: rotateX(-1deg) scale(0.95);
      background-color: rgb(50, 125, 255);
    }
  }
}

.version {
  color: #ffffff5e;
  font-style: italic;
  font-size: 14px;
  line-height: 23px;
  text-align: center;
}
</style>
<style>
body,
html,
p {
  /* min-height: 248px; */ /*for wps*/
  overflow: hidden; /*for wps*/
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}
</style>
